.btn_area {
  position: relative;
  display: flex;
  overflow: hidden;
  justify-content: center;
  margin-top: 25px;
  margin-bottom: 60px;
}

.btn_area .btn {
  font-size: 16px;
  font-weight: bold;
  position: relative;
  display: flex;
  overflow: hidden;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 60px;
  transition: 0.3s;
  color: #ffffff;
  border: 1px solid #26429c;
  border-radius: 0;
  outline: none;
  background: #26429c;
}

.btn_area .btn:hover {
  color: #26429c;
  background: #ffffff;
}

.btn_area .btn.square {
  justify-content: center;
  width: 320px;
  max-width: 320px;
  height: 50px;
  margin: 0 auto;
  color: #333333;
  border: 1px solid #333333;
  border-radius: 0;
  background: #ffffff;
}

.btn_area .btn.square::after {
  position: absolute;
  right: 1em;
}

.btn_area .btn_orange {
  background-color: #f8833e;
}

.btn_area .btn_black {
  background-color: #333333;
}

.more-btn {
  display: flex;
  align-content: center;
  align-items: center;
  flex-wrap: wrap;
  justify-content: center;
  margin: 0 auto;
  border: 1px solid #333333;
  border-radius: 0;
}

@media (max-width: 440px) {
  .more-btn {
    width: 90%;
  }
}

.more-btn .more {
  font-size: 16px;
  line-height: 2.2;
  position: relative;
  display: block;
  overflow: hidden;
  transition: 0.3s;
  text-align: center;
  border-radius: 0;
}

.more-btn .more:hover {
  font-style: italic;
  cursor: pointer;
  color: #ffffff !important;
}

.more-btn .more:hover a {
  color: #ffffff;
}

.more-btn .more:hover::after {
  left: -10%;
  width: 120%;
}

.more-btn .more::after {
  position: absolute;
  z-index: -1;
  bottom: 0;
  left: 50%;
  left: -10%;
  width: 0;
  height: 3px;
  height: 120%;
  content: "";
  transition: 0.3s;
  transform: skewX(15deg);
  background: #333333;
}

.btn-n {
  font-size: 16px;
  font-weight: bold;
  line-height: 1;
  position: relative;
  display: block;
  width: 100%;
  max-width: 320px;
  margin: 20px auto;
  padding: 15px;
  text-align: center;
  color: #ffffff;
  border: solid 2px #26429c;
  border-radius: 5px;
  background-color: #26429c;
}

.btn-n span {
  line-height: 1;
  position: absolute;
  top: 13px;
  right: 10px;
  display: block;
}

.btn-n:hover {
  color: #26429c;
  background-color: #eeeeee;
}

.btn-n::after {
  position: absolute;
  right: 10px;
}

